.text-subheading-medium {
	line-height: 16px;
}
.title-container {
	gap: 8px;
	flex-direction: column;
}
.table {
	border-radius: 4px;
	border: 1px solid var(--neutral-2);
}
.btn-subscribe {
	height: 40px;
	width: 223px;
	border-radius: 4px;
}
table {
	td,
	th {
		border: 1px solid var(--neutral-2) !important; /* Light grey border */
		padding: 16px 12px 16px 16px !important;
	}

	th.subscription-offers,
	td.subscription-offers {
		max-width: 638px;
		word-wrap: break-word;
	}

	td.subscription-offers > findqo-subscription-offers {
		display: block;
		margin-right: 180px;
	}
}
